import React from "react";

import projects from "./header/projects";
import 'bootstrap/dist/css/bootstrap.css';
import ProjectHeader from "./header/ProjectHeader";
import TaskListHeader from "./header/TaskListHeader";
import GetAllTasksWithProjectID from "./header/GetAllTasksWithProjectID";
import * as axios from "axios";


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            total: null,
            next: null,
            operation: null,
        };
    }

    state = {
        projects: []
    };

    componentDidMount() {
        axios.get(`http://localhost:8090/projects/`)
            .then(res => {
                const projects = res.data;
                console.log(projects);
                this.setState({projects});
            })
    }

    render() {
        return (
            <div>
                <ProjectHeader projects={projects}/>
                <hr/>

                {/*<TaskListHeader articles={articles}/>*/}

                <GetAllTasksWithProjectID/>


            </div>
        );
    }
}

export default App;



